import React from "react";
import { Container } from "@mui/material";

import DrawerAppBar from "../components/Navbar";
import PageTitle from "../components/PageTitle";
import LotteryModeGamePage from "../components/LotteryModeGamePagePage";
import Footer from "../components/Footer";

const LotteryModeGame = () => {
  return (
    <div className="Whitepaper">
      <Container>
        <DrawerAppBar />
        <PageTitle pageTitle="MYLT Lottery Mode Game" />
        <LotteryModeGamePage/>
      </Container>
      <Footer />

    </div>
  );
};

export default LotteryModeGame;
