import React from "react";
import Grid from "@mui/material/Unstable_Grid2";

import "./Roadmap.css";
import { Container } from "@mui/system";

import marketPlace from "../../assets/images/Roadmap/Lottery.gif";
import nicheLottery from "../../assets/images/Roadmap/2.png";
import metaverse from "../../assets/images/Roadmap/3.png";

import line1 from "../../assets/images/Roadmap/line1.png";
import line2 from "../../assets/images/Roadmap/line2.png";
import line3 from "../../assets/images/Roadmap/line3.png";

const RoadmapPage = () => {
  return (
    <div className="RoadmapPage">
      <Container>
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
        >
          <Grid xs={12} sm={12} md={8} xl={8}>
            <p>
              The success of every NFT project is all about a clear vision. So
              we will share with you how we plan to change the NFT lottery
              industry with our unique MYLT project.
            </p>
          </Grid>

          <Grid xs={12} sm={12} md={10} xl={10}>
            <h1>What to Expect From Our Innovative MYLT NFT Lottery?</h1>
          </Grid>
        </Grid>
      </Container>

      <div className="initialmylt">
        <Container>
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid xs={12} sm={12} md={8} xl={8}>
              <h2>Initial MYLT Lotteries</h2>
            </Grid>
            <Grid xs={12} sm={12} md={10} xl={10}>
              <p>
                Our journey begins with our initial first few lotteries. They
                will have general rewards like NFTs, Rolex watches, or gadgets
                and fall into four categories: G0, G1, G2, and G3. Your chance
                of winning something in these lotteries varies between 25% and
                100%!
              </p>
              <p>
                {" "}
                And the best thing is that each lottery has exclusive token
                rewards you can use to buy tickets for the new lotteries. In
                other words, it will be like a never-ending exciting game, each
                with a shot at the top prizes!
              </p>
            </Grid>
          </Grid>
        </Container>
      </div>

      <div className="roadmapDetails">
        <img src={line1} alt="line1" className="line1" />
        <Container>
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid xs={12} sm={12} md={4} xl={4}>
              <h3>Creating an NFT Lottery Tickets Marketplace</h3>
              <p>
                It might be tricky to Mint your lottery tickets on a webpage.
                After our initial first lotteries, this issue will be fixed as
                we will create our own marketplace. On this different platform,
                you can browse through all the available lotteries and quickly
                choose the one you want to try out the most.
              </p>
            </Grid>

            <Grid xs={12} sm={12} md={8} xl={8}>
              <img src={marketPlace} alt="marketPlace" className="roadmapImg" />
            </Grid>
          </Grid>

          <img src={line2} alt="line2" className="line2" />

          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid xs={12} sm={12} md={8} xl={8}>
              <img
                src={nicheLottery}
                alt="nicheLottery"
                className="roadmapImg"
              />
            </Grid>

            <Grid xs={12} sm={12} md={4} xl={4}>
              <h3>Introducing Niche Lotteries</h3>
              <p>
                Not two players think alike. Therefore, having the same general
                awards for our MYLT lotteries won't be optimal. So, in our
                marketplace, we will introduce various niche lotteries that
                cover shiny toys you can win, like watches, fine art, supercars,
                and whatever you can think of!
              </p>
            </Grid>
          </Grid>

          <img src={line3} alt="line3" className="line3" />

          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid xs={12} sm={12} md={4} xl={4}>
              <h3>Building Our Own Metaverse</h3>
              <p>
                Nothing beats the nerve-wracking emotions while playing a game
                of chance live. And the blockchain Metaverse is the only place
                you can get as close to that feeling as possible. After the
                initial success of our innovative lotteries, we plan to
                integrate them into our own Metaverse all our NFT holders can
                join. It will be plenty of fun!
              </p>
            </Grid>
            <Grid xs={12} sm={12} md={8} xl={8}>
              <img src={metaverse} alt="metaverse" className="roadmapImg" />
            </Grid>
          </Grid>
        </Container>
      </div>
    </div>
  );
};

export default RoadmapPage;
