import React, { useState, useCallback } from "react";

import PropTypes from "prop-types";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { Container } from "@mui/material";
import Modal from "@netojose/react-modal";
import Logo from "../../assets/images/logo.png";

import rainBow from "../../assets/images/ConnectWallet/rainbow.png";
import metaMask from "../../assets/images/ConnectWallet/metaMask.png";
import gnosis from "../../assets/images/ConnectWallet/Gnosis.png";
import argent from "../../assets/images/ConnectWallet/argent.png";

import "./Navbar.css";

const drawerWidth = 240;
const navItems = ["Home", "About", "Roadmap", "Lottery", "FAQ"];

function DrawerAppBar(props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const openModal = useCallback(() => setIsOpen(true), []);
  const closeModal = useCallback(() => setIsOpen(false), []);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: "center" }}>
      <Typography variant="h6" sx={{ my: 2 }}>
        <img src={Logo} alt="Logo" />
      </Typography>
      <Divider />
      <List>
        {navItems.map((item) => (
          <ListItem key={item} disablePadding>
            <ListItemButton sx={{ textAlign: "center" }}>
              <a href={item} className="navlinks mobileView">
                {item}
              </a>
            </ListItemButton>
          </ListItem>
        ))}
        <button className="connectWalletBtn" onClick={openModal}>
          Connect wallet
        </button>
      </List>
    </Box>
  );

  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: "flex" }}>
      <AppBar component="nav" color="transparent" position="absolute">
        <Container>
          <Toolbar>
            <IconButton
              color="error"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ mr: 2, display: { sm: "none" } }}
            >
              <MenuIcon />
            </IconButton>
            <Typography
              variant="h6"
              component="div"
              align="left"
              sx={{ flexGrow: 1, display: { xs: "none", sm: "block" } }}
            >
              <img src={Logo} alt="Logo" />
            </Typography>
            <Box sx={{ display: { xs: "none", sm: "block" } }}>
              {navItems.map((item) => (
                <Button key={item} sx={{ color: "#fff" }}>
                  <a href={item} className="navlinks">
                    {item}
                  </a>
                </Button>
              ))}

              <button className="connectWalletBtn" onClick={openModal}>
                Connect wallet
              </button>

              <Container>
                <Modal isOpen={isOpen} onRequestClose={closeModal}>
                  <div className="ModalConnectWallet">
                    <h1>Connect Wallet</h1>

                    <div class="container-block container">
                      <div class="responsive-container-block teamcard-container">
                        <div class="responsive-cell-block wk-desk-3 wk-mobile-12 wk-tab-4 wk-ipadp-4 team-card-container">
                          <div class="team-card">
                            <div class="team-card-content">
                              <p class="name">Rainbow</p>
                            </div>
                            <div class="team-img-wrapper">
                              <img src={rainBow} alt="Token" class="team-img" />
                            </div>
                          </div>
                        </div>
                        <div class="responsive-cell-block wk-desk-3 wk-mobile-12 wk-tab-4 wk-ipadp-4 team-card-container">
                          <div class="team-card">
                            <div class="team-card-content">
                              <p class="name">MetaMask</p>
                            </div>
                            <div class="team-img-wrapper">
                              <img
                                src={metaMask}
                                alt="Token"
                                class="team-img"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="responsive-cell-block wk-desk-3 wk-mobile-12 wk-tab-4 wk-ipadp-4 team-card-container">
                          <div class="team-card">
                            <div class="team-card-content">
                              <p class="name">Gnosis Safe</p>
                            </div>
                            <div class="team-img-wrapper">
                              <img src={gnosis} alt="Token" class="team-img" />
                            </div>
                          </div>
                        </div>
                        <div class="responsive-cell-block wk-desk-3 wk-mobile-12 wk-tab-4 wk-ipadp-4 team-card-container">
                          <div class="team-card">
                            <div class="team-card-content">
                              <p class="name">Argent</p>
                            </div>
                            <div class="team-img-wrapper">
                              <img src={argent} alt="Token" class="team-img" />
                            </div>
                          </div>
                        </div>

                        <p className="qrCode">View QR Code instead</p>
                      </div>
                    </div>
                  </div>
                </Modal>
              </Container>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>

      <Box component="nav">
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
        >
          {drawer}
        </Drawer>
      </Box>
    </Box>
  );
}

DrawerAppBar.propTypes = {
  window: PropTypes.func,
};

export default DrawerAppBar;
