import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

import HomepageMain from "./HomepageMain";
import MintPage from "./MintPage";
import AboutUs from "./AboutUs";
import Faq from "./Faq";
import Roadmap from "./Roadmap";
import LotteryModeGame from "./LotteryModeGame";
import StatisticOfLottery from "./StatisticOfLottery";

const AllRoutes = () => {

  return (
    <div>
      <Router>

        <Routes>
          <Route path="/" exact element={<HomepageMain />}/>
          <Route path="/Home" exact element={<HomepageMain />} />
          <Route path="/mint" exact element={<MintPage />} />
          <Route path="/About" exact element={<AboutUs />} />
          <Route path="/FAQ" exact element={<Faq />} />
          <Route path="/Roadmap" exact element={<Roadmap />} />
          <Route path="/LotteryModeGame" exact element={<LotteryModeGame />} />

          <Route path="/Lottery" exact element={<StatisticOfLottery />} />

        </Routes>
      </Router>
    </div>
  );
};

export default AllRoutes;
