import React from "react";
import { useNavigate } from "react-router-dom";
import Grid from "@mui/material/Unstable_Grid2";
import { Container } from "@mui/material";

import { Player, BigPlayButton } from "video-react";

import NFTvideo from "../../assets/video/MYLT_Subtitles.mp4";
import step1 from "../../assets/images/Homepage/1.png";
import step2 from "../../assets/images/Homepage/2.png";
import step3 from "../../assets/images/Homepage/3.png";
import line from "../../assets/images/Homepage/line.png";
import LotteryImg from "../../assets/images/PURPLE.gif";

import Timeline from "../Timeline";

import discord from "../../assets/images/Homepage/discord.svg";
import instagram from "../../assets/images/Homepage/insta.svg";
import twitter from "../../assets/images/Homepage/twitter.svg";

import "./Homepage.css";
import SimpleSlider from "../Slider";
import Footer from "../Footer";
import SimpleSlider2 from "../Slider2";

const Homepage = () => {

  let navigate = useNavigate(); 
  const routeChange = () =>{ 
    let path = `/mint`; 
    navigate(path);
  }

  return (
    <div className="Homepage">
      <Player src={NFTvideo}>
        <BigPlayButton position="center" />
      </Player>
      <div className="stepsHomepage">
        <Container>
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
            columns={{ xs: 12, sm: 8, md: 12 }}
          >
            <Grid xs={12} sm={12} md={8} xl={6}>
              <h1>MYLT Your Lottery Game Ticket</h1>
            </Grid>

            <Grid xs={12} sm={12} md={10} xl={8}>
              <p>
                MYLT is the company to release the first-ever Web 3.0-based NFT
                lottery, where you can constantly flip your tickets for profit
                even before the draw. Lottery winnings aren’t all about luck
                anymore. It’s time to take control. We developed our project on
                the Ethereum blockchain to provide our players with complete
                freedom and the most advanced security online.
              </p>
              <p>
                Winning From the Lottery is Common at MYLT Our lotteries come
                with a significant winning chance of 25% to 100% Buying an NFT
                ticket shouldn't be stressful, it should be exciting!
              </p>
            </Grid>
          </Grid>

          {/* Winning Probablity steps 1 2 3 */}
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid item xs={12} md={12} lg={2.4}>
              <div className="steps">
                <img src={step1} alt="step 1" />
                <h1>Mylt a ticket</h1>
                <p>
                  Mint your first lottery ticket to participate in our draw for
                  the top prizes
                </p>
              </div>
            </Grid>
            <Grid item xs={12} md={12} lg={2.4}>
              <div className="stepsLine">
                <img src={line} alt="line" />
              </div>
            </Grid>
            <Grid item xs={12} md={12} lg={2.4}>
              <div className="steps">
                <img src={step2} alt="step 2" />
                <h1>What do you win?</h1>
                <p>Learn how valuable is your lottery ticket</p>
              </div>
            </Grid>
            <Grid item xs={12} md={12} lg={2.4}>
              <div className="stepsLine">
                <img src={line} alt="line" />
              </div>
            </Grid>
            <Grid item xs={12} md={12} lg={2.4}>
              <div className="steps">
                <img src={step3} alt="step 3" />
                <h1>Auction Phase</h1>
                <p>
                  Decide if you want to keep your item or auction it for profit
                </p>
              </div>
            </Grid>
          </Grid>
        </Container>
      </div>

      {/* visual map  */}
      <div className="visualMap">
        <Container>
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid item xs={12} sm={12} md={8} lg={6}>
              <h1>The Interstellar Lottery</h1>
            </Grid>

            <Grid item xs={12} sm={12} md={10} lg={10}>
              <p>
                There isn’t a similar NFT project on Earth. Our lottery might
                not actually come from this planet. The cosmic NFT tickets you
                can get your hands on are one of a kind. As rare as the
                antimatter you can find on Earth, you can flip these valuable
                tickets for profit immediately after minting them. The initial
                Interstellar lottery awards that will soon cross the paths of
                Earth all come from the best Web 3.0 NFT project in the world -
                Yuga Labs.
              </p>
            </Grid>
          </Grid>
        </Container>
      </div>
      <Timeline />
      <button onClick={routeChange}>Mint</button>

      <div className="winningTicket">
        <Container>
          <h1>75%+ Winning Tickets</h1>
          <p>
            There aren’t any lotteries around with over 75% winning tickets. The
            significant chance of winning will act as if the big bang was on
            Earth, unleashing powerful waves of players who constantly flip
            their tickets for profit. All in search of the enormous jackpot
            prize coming from the depths of the galaxy. The NFT supply for our
            lottery is only 2,000 tickets, so hurry up before they are all gone.
          </p>

          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid item xs={12} sm={8} md={6} lg={4}>
              <img src={LotteryImg} alt="LotteryImg" />
            </Grid>
          </Grid>
        </Container>
      </div>

      <Container>
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
        >
          <Grid item xs={12} sm={8} md={8} lg={6}>
            <h1 className="cheaperTicket">
              Cheaper Tickets in Our Community Whitelist
            </h1>
          </Grid>

          <Grid item xs={12} sm={12} md={10} lg={10}>
            <p className="abtCheaperTicket">
              With how valuable each NFT ticket in our Interstellar lottery can
              prove, leaving all the other players snatching them away from you
              isn’t a wise decision. For our first lottery, 800 addresses from
              our community whitelist will receive the chance to exploit three
              unique bundle offers to increase their chance of winning. The
              maximum number of tickets that can be sold at bundle prices is
              only 900.
            </p>
          </Grid>
        </Grid>

        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
        >
          <Grid item xs={12} sm={12} md={6} lg={4}>
            <div className="cardsHomepage">
              <div className="cardsNfts">
                <p>Buy 1 </p>
                <p>NFT tickets at</p>
                <h1>0.6 ETH</h1>
              </div>
            </div>
          </Grid>

          <Grid item xs={12} sm={12} md={6} lg={4}>
            <div className="cardsHomepage">
              <div className="cardsNfts">
                <p>Buy 1 </p>
                <p>NFT tickets at</p>
                <h1>0.6 ETH</h1>
              </div>
            </div>
          </Grid>

          <Grid item xs={12} sm={12} md={6} lg={4}>
            <div className="cardsHomepage">
              <div className="cardsNfts">
                <p>Buy 1 </p>
                <p>NFT tickets at</p>
                <h1>0.6 ETH</h1>
              </div>
            </div>
          </Grid>
        </Grid>

        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
        >
          <Grid item xs={12} sm={12} md={10} lg={10}>
            <h1 className="cheaperTicket">
              We Have the Support of Leading Crypto Content Creators
            </h1>
          </Grid>

          <Grid item xs={12} sm={12} md={10} lg={10}>
            <p className="abtCheaperTicket">
              You are lucky to have come across the MYLT project before
              launching our first Web 3.0 NFT lottery. Not only are the
              Interstellar lottery’s mechanics innovative, but they will also
              provide players with complete freedom to acquire profit and have
              fun. So, get on board in our spaceship and let’s change the NFT
              lottery industry forever.
            </p>
          </Grid>
        </Grid>
      </Container>
      <SimpleSlider />

      <SimpleSlider2 />

      <div className="outerSpace">
        <Container>
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
          >
            <Grid item xs={12} sm={12} md={10} lg={10}>
              <h1>See You in Outer Space</h1>
              <p>
                Join our unique Discord community to follow our project's latest
                updates and not miss anything essential.
              </p>
            </Grid>
            <button>Join Discord</button>
          </Grid>
          <div className="lastIcons">
            <img src={discord} alt="discord" />
            <img src={instagram} alt="instagram" />
            <img src={twitter} alt="twitter" />
          </div>
        </Container>
        
      </div>
      <Footer/>

    </div>
  );
};

export default Homepage;
