import React from "react";
import { Container } from "@mui/material";

import DrawerAppBar from "../components/Navbar";
import PageTitle from "../components/PageTitle";
import StatisticOfLotteryPage from "../components/StatisticOfLotteryPage";


import Footer from "../components/Footer";

const StatisticOfLottery = () => {
  return (
    <div className="StatisticOfLottery">
      <Container>
        <DrawerAppBar />
        <PageTitle pageTitle="Statistic Of Lottery" />
        <StatisticOfLotteryPage/>
      </Container>
      <Footer />
    </div>
  );
};

export default StatisticOfLottery;
