import React, { useState } from "react";
import Grid from "@mui/material/Unstable_Grid2";

import Lottery1 from "../../assets/images/PURPLE.gif";

import "./MintPageBanner.css";

const MintPageBanner = () => {
  let [num, setNum] = useState(0);
  let [lotteryPrice, setLotteryPrice] = useState(0.5);

  let incNum = () => {
    setNum(Number(num) + 1);
    setLotteryPrice(lotteryPrice + 0.5);
  };
  let decNum = () => {
    if (num > 0) {
      setNum(num - 1);
      setLotteryPrice(lotteryPrice - 0.5);
    }
  };
  let handleChange = (e) => {
    setNum(e.target.value);
  };
  return (
    <div className="Banner">
      <Grid
        container
        direction="row"
        justifyContent="flex-end"
        alignItems="center"
        columns={{ xs: 4, sm: 8, md: 12 }}
      >
        <Grid xs={4}>
          <div className="ticketDetails">
            <h3>
              1000+ <br /> <span>Tickets Available</span>
            </h3>
            <h3>
              500 <br /> <span>Sold Out</span>
            </h3>
          </div>
        </Grid>

        <Grid xs={4}>
          <div className="Lottery">
            <img src={Lottery1} alt="Lottery#1" />
          </div>
        </Grid>
        <Grid xs={4} alignItems="left">
          <div className="LotteryPrice">
            <p>Price</p>
            <h3>{lotteryPrice} ETH</h3>
          </div>
        </Grid>
      </Grid>
      <div className="IncDecInput">
        <button onClick={incNum}>+</button>
        <input type="text" value={num} onChange={handleChange} />
        <button onClick={decNum}>-</button>
      </div>
      <button className="BuyYouMint">Buy Your Mint</button>
    </div>
  );
};

export default MintPageBanner;
