import React from "react";
import Grid from "@mui/material/Unstable_Grid2";

import "./AboutMint.css";

const AboutMint = () => {
  return (
    <div className="AboutMint">
      <h1>Mylt Your Lottery Ticket Now!</h1>
     
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="center"
        columns={{ xs: 12, sm: 12, md: 12 }}
      >
        <Grid xs={8}>
        <p>
        Sometimes, winning the lottery is all about SPEED. Will you let all the
        other players exploit the bundle offers and increase their winning
        chances while you hesitate? The stakes are high, and there is a shortage
        of top prizes. So, embrace your luck and mint as many lottery tickets as
        possible before they are gone!
      </p>
        </Grid>
      </Grid>
    </div>
  );
};

export default AboutMint;
